<template>
  <div class>
    <h2>todo list</h2>
    <ul>
      <li v-for="todo in list" :key="todo.id">{{todo.text}}</li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      list: []
    };
  },
  created() {
    this.$http.get("/todos").then(res => {
      console.log("todos> ", res.data);
    });
  }
};
</script>

